import axios from 'axios';
import { createAsyncThunk } from '@reduxjs/toolkit';

import setAxiosDefaults from '../setAxiosDefaults';

setAxiosDefaults();

// Se definete si se exporta o aciune asincron numit 'fetchContacts' folosind createAsyncThunk:
export const fetchContacts = createAsyncThunk(
  'contacts/fetchAll', // Numele aciunii (n acest caz, 'contacts/fetchAll')

  // Callback asincron invocat cnd aciunea este declanat (dispatch):
  async (_, thunkAPI) => {
    try {
      // Se realizeaz o cerere GET ctre server pentru a obine datele despre contacte:
      const response = await axios.get('/contacts');

      // Afieaz datele n consol:
      console.log('Datele din API:', response.data);

      // Returneaz datele obinute de la server:
      return response.data;
    } catch (error) {
      // n caz de eroare, se respinge cu o valoare care include mesajul de eroare:
      return thunkAPI.rejectWithValue(error.message);
    }
  }
);

// ==== Actiunea asincrona addContact: ====
export const addContact = createAsyncThunk(
  'contacts/addContact',
  async (contact, thunkAPI) => {
    try {
      const response = await axios.post('/contacts', contact);
      return response.data;
    } catch (error) {
      return thunkAPI.rejectWithValue(error.message);
    }
  }
);

// ==== Actiunea asincrona deleteContact: ====
export const deleteContact = createAsyncThunk(
  'contacts/deleteContact',
  async (contactId, thunkAPI) => {
    try {
      const response = await axios.delete(`/contacts/${contactId}`);
      return response.data;
    } catch (error) {
      return thunkAPI.rejectWithValue(error.message);
    }
  }
);

// ==== Actiunea asincrona editContact: ====
export const editContact = createAsyncThunk(
  'contacts/editContact',
  async (data, thunkAPI) => {
    try {
      const response = await axios.patch(`/contacts/${data.id}`, {
        name: data.name,
        number: data.number,
      });
      return response.data;
    } catch (error) {
      return thunkAPI.rejectWithValue(error.message);
    }
  }
);
